import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Websocket from 'react-websocket';
//import $ from 'jquery';
import classNames from 'classnames';
import Item from './components/item.js';
import logo from './images/logo.png';
import './index.css';
import './App.css';
// import bulma css
import 'bulma/css/bulma.css'
// import react-highcharts
import ReactHighcharts from 'react-highcharts';
import {reverse, prepend, append, drop, dropLast, last} from 'ramda'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      mainData : {},
      transHistory : [],
      nightMode : false,
      hashrateData : [],
      hashrateChart : {},
      blocktimeData : [],
      blocktimeChart : {},
      timestamp : 0
    }
  }
  componentWillMount() {
    fetch("stats.json")
    .then(response => response.json())
    .then(json => {
      let bTimes = json.block_data.map((value, index) => {
        if (index > 0) {
          //console.log(json.block_data[(index-1)].age + ' - ' + value.age);
          let total = value.age - json.block_data[(index-1)].age,
              date = new Date(total * 1000);

          return parseFloat(date.getMinutes() + '.' + date.getSeconds());
        }
      });

      this.setState({
        mainData : {
          totalsupply : json.totalsupply,
          difficulty : json.difficulty,
          blocks : json.blocks,
          blocktime : json.blocktime,
          blockreward : json.blockreward,
          nethashrate : json.nethashrate
        },
        transHistory : reverse(json.block_data),
        hashrateData : reverse(json.netahashrate_log),
        blocktimeData : dropLast(1, reverse(bTimes))
      }, () => {
        this.getChartConfig('hashrate');
        this.getChartConfig('blocktime');
      });
    })
    .catch(error => console.log(error));
  }
  componentDidMount()
  {
    this.setState({ timestamp: new Date() }, () => {
      setInterval(() => this.setState({ timestamp: new Date() }), 10000);
    });
  }
  getChartConfig(chart) {
    let data = [], chartV = '';

    switch (chart) {
      case 'hashrate':
        data = this.state.hashrateData;
        chartV = 'hashrateChart';
        break;
      case 'blocktime':
        data = this.state.blocktimeData;
        chartV = 'blocktimeChart';
        break;
      default:

    }
    let chartConfig = {
      chart: {
        height: 190,
        backgroundColor: this.state.nightMode ? '#111111' : '#FFFFFF'
      },
      credits: {
          enabled: 0
      },
      title: {
        text: null
      },
      series: [{
          name: chart,
          type: 'column',
          colorByPoint: false,
          data: data,
          showInLegend: false,
          pointPadding: 0,
          groupPadding: 0.01
      }],
      yAxis: {
        visible: false
      },
      xAxis: {
        visible: false
      },
      plotOptions: {
        borderWidth : 0
      },
      column: {
        groupPadding : 0
      }
    };

    this.setState({
      [chartV] : chartConfig
    })
  }

  handleData = (data) => {
    let result = JSON.parse(data),
        stamp1 = new Date().getTime(),
        stamp2 = new Date((last(this.state.transHistory).age) * 1000).getTime(),
        date = new Date((stamp1 - stamp2) / 1000),
        bt = parseFloat(typeof result.blocktime != 'undefined' ? result.blocktime : date.getMinutes() + '.' + date.getSeconds());

    if(typeof result.totalsupply != 'undefined') {
      this.setState({
        mainData : {
          totalsupply : typeof result.totalsupply != 'undefined' ? result.totalsupply : this.state.mainData.totalsupply,
          difficulty : typeof result.difficulty != 'undefined' ? result.difficulty : this.state.mainData.difficulty,
          blocks : typeof result.blocks != 'undefined' ? result.blocks : this.state.mainData.blocks,
          blocktime : bt,
          blockreward : typeof result.blockreward != 'undefined' ? result.blockreward : this.state.mainData.blockreward,
          nethashrate : typeof result.nethashrate != 'undefined' ? result.nethashrate : this.state.mainData.nethashrate,
        }
      });
    }
    if(typeof result.block_data != 'undefined') {
      let blockList = dropLast(1, prepend(result.block_data, this.state.transHistory));

      this.setState({
        transHistory : blockList
      });
    }
    if(typeof result.netahashrate_log != 'undefined') {
      let hashList = append(result.netahashrate_log, drop(1, this.state.hashrateData)),
          blockList = append(bt, drop(1, this.state.blocktimeData));

      this.setState({
        hashrateData : hashList,
        blocktimeData : blockList
      }, () => {
        this.getChartConfig('hashrate');
        this.getChartConfig('blocktime');
      });
    }
    console.log(result);
  };
  _handleNightMode = (e) => {
    e.preventDefault();

    this.setState({nightMode : !this.state.nightMode});

    let nightModeChartConfigHash = this.state.hashrateChart;
    let nightModeChartConfigBlock = this.state.blocktimeChart;

    nightModeChartConfigHash.chart.backgroundColor = !this.state.nightMode ? '#111111' : '#FFFFFF';
    nightModeChartConfigBlock.chart.backgroundColor = !this.state.nightMode ? '#111111' : '#FFFFFF';

    this.setState({
      hashrateChart : nightModeChartConfigHash,
      blocktimeChart : nightModeChartConfigBlock
    });
  };
  renderHistory() {
    let rows = [];
    if(this.state.transHistory.length > 0) {
      let date = new Date(),
          timestamp = date.getTime();

      rows = this.state.transHistory.map((value, index) => {
        let a = this.state.timestamp,
            b = new Date(value.age*1000);

        return (
          <tr key={index}>
            <td className="text-center">{value.height}</td>
            <td className="text-center">{timeFormat((a.getTime()  - b.getTime())/1000)}</td>
            <td className="text-center">{value.size}</td>
            <td className="text-center">{value.transactions}</td>
            <td className="text-center">{value.sent}</td>
          </tr>
        );
      });
    }else{
      rows = <tr><td colSpan={5} className="text-center">fetching data...</td></tr>;
    }

    return rows;
  }
  render() {
    let containerClass = classNames({
        'nightMode' : this.state.nightMode
    });
    document.body.className = containerClass;
    return (
      <div className='container'>
        <Websocket url='ws://159.203.58.37:8080' onMessage={this.handleData}/>
        <div className="columns">
          <div className="column is-one-quarter">
            <div style={{textAlign: 'center', padding: 10}}>
              <img src={logo} alt="Verium" style={{width : 130, paddingBottom : 20}}/>
              <br/>
              <h2 className="title">MINING STATISTICS</h2>
            </div>
            <Item icon={'check-circle-o'} title="Current Supply">
              {toFixed(this.state.mainData.totalsupply, 2)} VRM
            </Item>
            <Item icon={'globe'} title="Network Hash">
              {toFixed(parseFloat(this.state.mainData.nethashrate), 2)} kH/m
            </Item>
            <Item icon={'star'} title="Mining Reward">
              {toFixed(parseFloat(this.state.mainData.blockreward), 2)} VRM
            </Item>
            <Item icon={'cogs'} title="Blocks">
              {this.state.mainData.blocks}
            </Item>
            <Item icon={'clock-o'} title="Block Time">
              {this.state.mainData.blocktime}
            </Item>
            <Item icon={'line-chart'} title="Network Difficulty">
              {toFixed(parseFloat(this.state.mainData.difficulty), 6)}
            </Item>
          </div>
          <div className="column">
            <button className="button" style={{position: 'absolute', top: 30, right: 0, zIndex: 999}} onClick={this._handleNightMode}>Night Mode: {this.state.nightMode ? 'ON' : 'OFF'}</button>
            <div style={{textAlign : 'center', paddingTop: 30}}>
              <div id="map" style={{height: 400, zIndex: 0}}></div>
            </div>
            <div className="columns">
              <div className="column is-half">
                <h3>Blocktime</h3>
                <div className="box">
                  <article className="media">
                    <div className="media-content">
                      <div className="content">
                        <ReactHighcharts config={this.state.blocktimeChart}></ReactHighcharts>
                      </div>
                    </div>
                  </article>
                </div>
              </div>
              <div className="column is-half">
                <h3>Net hashrate</h3>
                <div className="box">
                  <article className="media">
                    <div className="media-content">
                      <div className="content">
                        <ReactHighcharts config={this.state.hashrateChart}></ReactHighcharts>
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="columns">
          <div className="column">
            <h3>Transaction history</h3>
            <table className="table text-center">
              <thead>
                <tr>
                  <th className="text-center">Height</th>
                  <th className="text-center">Age</th>
                  <th className="text-center">Size</th>
                  <th className="text-center">Transactions</th>
                  <th className="text-center">Sent</th>
                </tr>
              </thead>
              <tbody>
                {this.renderHistory()}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}
function toFixed(num, fixed) {
    if(num > 0) {
      var re = new RegExp('^-?\\d+(?:\.\\d{0,' + (fixed || -1) + '})?');
      return num.toString().match(re)[0];
    }else{
      return num;
    }
}
function timeFormat(a) {
  return 60 > a ? "< 1 minute ago" : 3600 > a ? Math.floor(a / 60) + " minute" + (120 > a ? "" : "s") + " ago" : 86400 > a ? Math.floor(a / 60 / 60) + " hour" +
    (7200 > a ? "" : "s") + " ago" : Math.floor(a / 60 / 60 / 24) + " day" + (172800 > a ? "" : "s") + " ago"
}

ReactDOM.render(
  <App />,
  document.getElementById('root')
);



